.profile-body{
  background-color: #0fff00;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Oswald', sans-serif;
}

.profile-picture{
  border-radius: 100%;
  height: 200px;
  width: 200px;
}

.navbar{ 
  padding: 1rem;
  background-color: rgb(50, 50, 35);
}



a {
  color: #333;
  text-decoration: none;
}

.container {
  max-width: 1100px;
  margin: auto;
  overflow: auto;
  padding: 0 2rem;
}

.main-header {
  height: 55vh;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.main-header h1 {
  font-size: 4rem;
  margin-bottom: 2rem;
  line-height: 1.2;
}

.main-header h1 span {
  color: #b50d10;
}

.main-header p {
  font-size: 2rem;
}

img {
  width: 100%;
}

.card {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  background: #ffe;
  margin-bottom: 2rem;
}

.card h3 {
  margin-bottom: 2rem;
}


.card > div {
  padding: 2rem;
}

.card:nth-child(even) img {
  order: 2;
}


.btn {
  display: inline-block;
  background: #000;
  color: #fff;
  padding: 0.8rem 1.8rem;
  margin-top: 2rem;
  cursor: pointer;
  margin-right: 1rem;
}

.btn:hover {
  opacity: 0.8;
}

.button-row{ 
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.switch{ 
  height:50px;
  width:80px;
  background-color: rgb(154, 154, 52);
  display: flex;
  justify-content: flex-start; 
  border-radius: 50px;
  padding:10px;
  cursor: pointer;

}

.switch[data-ison="true"]{
  justify-content: flex-end;
  background-color: rgb(50, 50, 35);
}

.handle{
  width: 30px;
  background-color: wheat;
  border-radius: 40px;
}

@media(max-width:600px) {
  .card {
    display: block;
  }
  .profile-picture{
      margin-top: 8rem;
  }
}

.sticky{ 
  position: sticky;
  top: 0;
  z-index: 100;
}