 .dark-profile-body{
    background-color: #000;
 }

  .navbar{ 
    padding: 1rem;
    background-color: rgb(50, 50, 35);
  }

  
  a {
    color: #333;
    text-decoration: none;
  }


  .main-header h1 span {
    color: #b50d10;
  }
  
  .dark-card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    background: #540c0c;
    margin-bottom: 2rem;
  }
  
  .dark-card h3 {
    margin-bottom: 2rem;
  }
  
  
  .dark-card > div {
    padding: 2rem;
  }
  
  .dark-card:nth-child(even) img {
    order: 2;
  }
  
  
  .btn {
    display: inline-block;
    background: #000;
    color: #fff;
    padding: 0.8rem 1.8rem;
    margin-top: 2rem;
    cursor: pointer;
    margin-right: 1rem;
  }


  
  @media(max-width:600px) {
    .dark-card {
      display: block;
    }
  
  }
